<template>
    <div class="reports-list">
        <div class="pure-menu pure-menu-horizontal  tar mgb1">
            <ul class="pure-menu-list">
                <li :class="['pure-menu-item',  {'pure-menu-selected':(keys=='dt'&&orders!='desc')}]"><a class="pure-menu-link" @click="sortItems('dt')">最远</a></li>
                <li :class="['pure-menu-item',  {'pure-menu-selected':(keys=='dt'&&orders=='desc')}]"><a class="pure-menu-link" @click="sortItems('dt', 'desc')">最新</a></li>
                <!-- <li class="pure-menu-item"><a class="pure-menu-link" @click="sortItems(['title'])">Name</a></li> -->
                <!-- <li :class="['pure-menu-item',  {'pure-menu-selected':(keys=='placeName')}]"><a class="pure-menu-link " @click="sortItems('placeName')">地区</a></li> -->
                <li class="pure-menu-item"><a class="pure-menu-link" @click="toggleGrid">{{showGrid?"列表":"图墙"}}</a></li>
            </ul>
        </div>
        <div v-if="showGrid" class="img-rows pure-g">
            <div class="img-card" v-for="(item, index)  in rs" :key="item.id">
                <router-link :to="'/report/'+item.id">
                    <img :src="item.p|ISU" :alt="item.t" :id="'img-'+item.id">
                </router-link>
                
            </div>
        </div>
        <div class="post-list" v-if="!showGrid">
            <section class="post" v-for="(item, index)  in rs" :key="item.id">
                <!--                <header class="post-header">
                    <img width="48" height="48" class="post-avatar" :src="item.thumb|ISU" :alt="item.title" :id="'img-'+item.id">
                    
                </header> -->
                <div class="post-description">
                    <div class="post-images pure-g">
                        <div class="pure-u-1 pure-u-md-1-3">
                            <router-link :to="'/report/'+item.id">
                                <img class="pure-img pdb1" :src="item.p|ISU" :alt="item.t" :id="'img-'+item.id">
                            </router-link>
                            <!-- <div class="post-image-meta">
                                <h3>{{item.title}}</h3>
                            </div> -->
                        </div>
                        <div class="pure-u-1 pure-u-md-2-3">
                            <h2 class="post-title">{{item.t}}</h2>

                            <div class="post-meta">
                                <rlink class="ti tsmall" :item="item" k="cl" path="reports"></rlink>
                                <rlink class="ti tsmall" :item="item" k="od" path="reports"></rlink>
                                <rlink class="ti tsmall" :item="item" k="fm" path="reports"></rlink>
                                <rlink class="ti tsmall" :item="item" k="sn" path="reports"></rlink>
                                <p class="mgb05">
                                    By <a class="post-author" href="#">{{item.s}}</a> at {{item.dt}}
                                    <!-- <a class="post-category" href="#">{{item.placeName}}</a> -->
                                </p>
                                <rlink class="post-category mgb05" :item="item" k="ds" path="reports">efjeiifei</rlink>
                                <rlink class="post-category mgb05" :item="item" k="region" path="reports"></rlink>

                                <rlink class="post-category mgb05" :item="item" k="country" path="reports"></rlink>
                                <!-- <rlink class="post-category" :item="item" k="placeName" path="reports">efjeiifei</rlink> -->
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </div>
        <div v-if="!showGrid&&false" class="list-block media-list thumb-list">
            <ul>
                <li v-for="(item, index)  in rs" :key="item.id">
                    <router-link :to="'report/' + item.id">
                        <div class="item-content">
                            <div class="item-media">
                                <!-- <img class=lazy :data-src="item.thumb|ISU" :id="'img-'+item.id"> -->
                                <img :src="item.thumb|ISU" :alt="item.title" :id="'img-'+item.id">
                            </div>
                            <div class="item-inner">
                                <div class="item-title-row">
                                    <div class="item-title">{{item.title}}</div>
                                    <!-- <div class="item-after">Element label</div> -->
                                </div>
                                <div class="item-subtitle">
                                    {{item.scientificName}} </div>
                                <div class="item-text">{{item.source}} @ {{item.timeReport}}
                                    <!--                                          <div class="chip">
                                            <div class="chip-media bg-blue"><i class="icon material-icons">today</i></div>
                                            <div class="chip-label">{{item.timeReport}}</div>
                                          </div> -->
                                    <br/>
                                    <!--                                       <div class="chip">
                                        <div class="chip-media bg-blue"><i class="icon material-icons">add_location</i></div>
                                        <div class="chip-label">{{item.divesite}}</div>
                                      </div> -->
                                    {{item.placeName}} {{item.divesite}}
                                </div>
                            </div>
                        </div>
                    </router-link>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
    props: {
        items: {
            type: Array,
            required: true
        },
        grid:{
            type: Boolean,
        },
        k:{
            type: String,
            default : 'dt'
        },
        o:{
            type: String,
            default : 'desc'
        },

    },
    data() {

        var marine;
        var q, rank, name, reports;

        return {
            // items: this.reports,
            marine: marine,
            name: name,
            page: 1,
            reportsPerPage: 36,
            showSpinner: true,
            loading: false,
            showGrid: this.grid,
            sectionPlace: false,
            keys: [this.k],
            orders: [this.o],
            pswpId: "pswp-"
        }
    },
    computed:{
            rs: function(){
               var rs = this.sortItems(this.keys, this.orders)
               return rs;
            },


        },
    mounted() {
        console.log("reports-list.vue mounted")
    },
    created() {
        // 组件创建完后获取数据，
        // 此时 data 已经被 observed 了
        // this.fetchData()
    },
    methods: {
        toggleGrid(keys, orders) {
            this.showGrid = !this.showGrid;
        },
        sortItems(keys, orders) {
            var items = _.orderBy(this.items, keys, orders)
            this.keys = keys;
            this.orders = orders;
            // this.items = items;
            return items;
        },
    },


}
</script>
