<script>
// var Utility = require("../../utility.js");
console.log('in report ', this);
var TypeAhead = require('./assets/static/js/type-ahead.js')

// window.$$('a#download').on('click', function (e) { 
//   console.log('clicked download',e); 
// });
export default {
    // props: ['id'],

    mounted: function() {

        var input = document.getElementById("input-sn");
        new TypeAhead(input, this.$root.scientificNames);
        var tp = new TypeAhead(input, this.$root.scientificNames, {
            fulltext: true,
            limit: 10,
             minLength: 1
        });
        // new Awesomplete(input, {list: "#mylist"});
        console.log("report.vue", this, this.name, this.$route, input, tp)
            // this.$$('a#download').on('click', function(e) {
            //     console.log('clicked download', e);
            // });


    },
    data: function() {
        // return {};
        var self = this;
        var editing;
        var id = this.$route.params.id;
        var root = this.$root._data;
        // var reports = root.reports;
        // var item = _.find(reports, function(r){
        //     return ((r.id==self.id)||(r.id==Number.parseInt(self.id)));
        // });
        // var item = this.$root.getReport(id);
        var Q = this.$route.query;
        if (!id || Q.edit) {
            editing = true;
        }
        var item = {
            "cid": "1",
            "gid": "1",
            ds: "",
            c: "",
            // dt: d2s(new Date()),
            dt: (new Date()).toS(),
            s: "ss",
            t: "",
            sn: "",
            p: null
        };
        // if (this.id) {
        //     item = this.$root.getReport(this.id)
        // }
        // var item = this.initItem(false);
        return {
            // id: id,
            itemEdit: item,
            itemBack: null,
            editing: editing,
            divesite: null,
            Place: root.Place,
            Divesite : root.Divesite,

        }

    },
    computed: {

        id: function() {
            return this.$route.params.id;
        },

        country: function() {
            var r = _.filter(this.Place, {
                gid: '0'
            })
            return r;
            // return window.countryData;
        },
        region: function() {
            var self = this;
            // var r = _.find(window.countryData, {
            //     id: this.item.cid
            // })
            var r = _.filter(this.Place, function(p) {

                return (p.gid && p.gid != '0' && p.cid == self.item.cid);
            })
            console.log("compute region", r)
            return r
        },
        divesties: function() {
            var r = _.filter(this.Divesite, {
                cid: this.item.cid,
                gid: this.item.gid
            })

            // r = _.map(r, function(d){
            //     return d.ds;
            // })

            return r;
        },
        item: function() {
            console.log("coupute item", this.id);
            var item = this.itemEdit;
            if (this.id) {
                item = this.$root.getReport(this.id)
                this.itemBack = item;
            }
            return item;
        },

    },
    filters: {
        filter: function(items) {
            console.log("search filter", items, this.q);
            return items;
        },

    },
    watch: {
        // '$route': 'initItem'
        // 'item.gid' : 'onChangeRegion'
    },

    methods: {
        onSubmit() {
            var self = this;
            if (this.divesite && this.divesite.length > 1) {
                //todo : add divesite to db
                //todo : add new country and new place
                this.item.ds = this.divesite;
            }
            console.log("onSubmit", this.item);
            //   this.$root.updateReport(this.item).then(function(u){
            //        console.log("onSubmit" ,u);

            //    // self.item.p = p;


            // });

        },
        onFile(e) {


            var self = this;
