            let file = e.target.files[0];
            getImageTime(file, this.item);
            this.$root.upload(file).then(function(u) {
                var p = u.get('url');
                console.log("onFile", u, p, u.url);

                self.item.p = p;


            });
        },
        onChangeSn(e) {
            console.log("onChangeSn", e, e.target.value);
            this.item.sn = e.target.value


        },
        onChangeRegion(e) {
            console.log("onChangeRegion", e);
            // this.item.sn =  e.target.value
            var input = document.getElementById("input-ds");
            // new TypeAhead(input, this.$root.scientificNames);
            console.log("onChangeSn", input, this.divesties);

            var tp = new TypeAhead(input, this.divesties, {
                fullthext: true,
                limit: 10,
                minLength: 1
            });

        },
        // initItem : function(setThis = true) {
        //     console.log("$route change ", this.$route.params)
        //     var id = this.$route.params.id;
        //     var item;
        //     if (id) {
        //         item = this.$root.getReport(this.id)
        //     }else {
        //         item = {
        //         "cid": "1",
        //         "gid": "1",
        //         ds: "",
        //         c: "",
        //         dt: "",
        //         s: "ss",
        //         t: "",
        //         sn: "",
        //         };
        //     }
        //     if (setThis) this.item = item; 
        //     return item;

        // },
        // newItem: function() {
        //     var item = {
        //         "cid": "1",
        //         "gid": "1",
        //         ds: "",
        //         c: "",
        //         dt: "",
        //         s: "ss",
        //         t: "",
        //         sn: "",
        //     };
        //     return item;
        // },
        contentFilter: function(content) {
            console.log("befor filter", content);
            // content = "<h1>test</h1>"
            // content = content.replace(/<a.*?>(<img.*?>)<.*?>/gi, '$1');
            content = content.replace(/<a.*?>(<img.*?>)<.*?>/gi, '');
            content = content.replace(/<img.*?>/gi, '');
            console.log("after filter", content);

            return content;
        },
        photoCaption: function(pb) {
            console.log("photoCaption", this, pb)
            var index = "no"
            if (this.photoBrowser) {
                index = this.photoBrowser.activeIndex;
            }
            return "index is " + index;
        },
        taxTreeString: function(m) {
            var s = m.class + ' > ' + m.order + ' > ' + m.family + ' > ' + m.genus
                // var s = m.kingdom  + '>' + m.phylum + '>' + m.class + '>' + m.order + m.family + '>' + m.genus 
            return s;
        },
        openPhotoBrowser: function(rs, index) {
            console.log("openPhotoBrowser", rs, index)

            var self = this;
            index = index || 0;
            var photos = rs || this.items;
            // if (this.foundItems && this.foundItems.length > 0) {
            //     photos = this.foundItems;
            // } else {
            //     photos = this.items;
            // }
            console.log("openPhotoBrowser", photos, index)

            var toolbar = `<div class="toolbar tabbar">
                                <div class="toolbar-inner">
                                    <a href="#" class="link photo-browser-prev">
                                        <i class="icon icon-prev {{iconsColorClass}}"></i>
                                    </a>
                                    <a href="#" id="download">
                                        download
                                    </a>
                           
                                    <a href="#" class="link photo-browser-next">
                                        <i class="icon icon-next {{iconsColorClass}}"></i>
                                    </a>
                                </div>
                            </div>`
            var photoTemplate = `<div class="photo-browser-slide swiper-slide">
                                <span class="photo-browser-zoom-container">
                                    <img src="{{js "ISU(this.url)"}}">
                                </span>
                            </div>`
            this.photoBrowser = f7.photoBrowser({
                // onTap: self.onTapPhoto,
                // testdata: self.photoCaption,
                type: 'page',
                photos: photos,
                initialSlide: index,
                theme: 'dark',
                swipeToClose: false,
                // lazyLoading : true,
                // type: 'popup',
                photoTemplate: photoTemplate,
                // toolbarTemplate: toolbar,
                // captionTemplate: '<div class="photo-browser-caption" data-caption-index="{{@index}}">{{title}}@{{placeName}}</div>'
            });
            //     this.photoBrowser.onTap(swiper, event)
            //            this.$$('a#download').on('click', function (e) { 
            //   console.log('clicked download',e); 
            // });
            this.photoBrowser.open();
            // this.$refs.pb.open();
        },
        onInput: function(e) {
            console.log('onInput', e)
            this.q = e;
            this.page = 1;
        },
        onSearch: function(query, found) {
            console.log('search', query);
        },
        onClear: function(event) {
            console.log('clear');
            this.page = 1;

        },
        onEnable: function(event) {
            console.log('enable');
        },
        onDisable: function(event) {
            this.q = null;
            this.page = 1;

            console.log('disable');
        }
    }
}
</script>



