function pad(number) {
    if (number < 10) {
        return '0' + number;
    }
    return number;
}

Date.prototype.toS = function() {
    return this.getFullYear() +
        '-' + pad(this.getMonth() + 1) +
        '-' + pad(this.getDate()) +
        'T' + pad(this.getHours()) +
        ':' + pad(this.getMinutes())
        // + ':' + pad(this.getSeconds())
        // +
        // '.' + (this.getUTCMilliseconds() / 1000).toFixed(3).slice(2, 5) +
        // 'Z';
};


window.getImageTime = function(img, obj = null) {

    EXIF.getData(img, function() {
        console.log(EXIF.pretty(this));

        var t = EXIF.getTag(img, "DateTimeOriginal");
        if (t) {
            t = t.slice(0, 16)
            t = t.replace(/(\d{4}):(\d{2}):(\d{2})\W/, "$1-$2-$3T")
        }
        // var t2 = new Date(s)

        console.log("DateTimeOriginal", t)
            // console.log("Copyright ", EXIF.getTag(img, "Copyright"))

        var author = EXIF.getTag(img, "Artist");
        console.log("Artist ", author)


        if (obj) {
            obj.dt = t;
            obj.s = author;
        }
        return t;
    });
    // var EXIF = require("EXIF");

    // console.log("DateTimeOriginal" ,t)
    // alert(EXIF.pretty(this));
}

window.includesQ = function(object, field, q) {
    if (!q) return true;
    if (!object || !object[field]) return false;

    // var f = object[field];
    // if(Array.isArray(f))
    // {

    // }
    if (object[field].toLowerCase().includes(q.toLowerCase())) {

        return true;
    } else {
        return false;
    }
}
window.ISU = function(value) {
    // value = "http://localhost:8888/marine/uploadfile/2017/0302/20170302015145537.jpg"
    value = value ? value.replace("http://dive.u.qiniudn.com/", "http://localhost:8888/marine/") : value
        // return  (window.ImageServerUrl + value);
    return value
}
