Vue.component('rlink', {
    render: function(createElement) {
        // console.log("rlink", this.$slots.default)

        if (!this.item || !this.item[this.k]) {
            return null;
        }
        var url = `/${this.path}?${this.k}=${this.item[this.k]}`
        return createElement(
            'router-link', // tag name
            { attrs: { to: url } },
            this.item[this.k]
        )
    },
    // template: `<router-link">{{item[key]}}</router-link>`,
    // template: `<router-link v-if="item[key]"  :to="'reports?scientificName='+item.scientificName" replace>{{item.scientificName}}</router-link>`,
    props: {
        path: {
            type: String,
            required: true
        },
        item: {
            type: Object,
            required: true
        },
        k: {
            type: String,
            required: true
        },
    },
})
