<template>
    <div class="reports-page">
        <div class="pure-menu pure-menu-horizontal">
            <ul class="pure-menu-list">
                <li class="pure-menu-item pure-menu-selected"><a href="#" class="pure-menu-link">Home</a></li>
                <li class="pure-menu-item pure-menu-has-children pure-menu-allow-hover">
                    <!-- <a href="#" id="menuLink1" class="pure-menu-link">国家地区</a> -->
                    <router-link class="pure-menu-link" to="/reports?cid=8&gid=1">国家地区</router-link>
                    <ul class="pure-menu-children">
                        <li class="pure-menu-item"><a href="#" class="pure-menu-link">Email</a></li>
                        <li class="pure-menu-item" v-for="p in PlaceTree">
                            <router-link :to="'/reports?region='+ p.t" class="pure-menu-link">{{p.t}}</router-link>
                        </li>

                    </ul>

                </li>
                <router-link to="/reports?cid=8&gid=1" class="pure-menu-item pure-menu-allow-hover">ambon</router-link>

            </ul>
        </div>
        <div class="">
            <div v-for="c in PlaceTree">
                <router-link :to="'/reports?country='+ c.t" class="">{{c.t}}</router-link>
                <router-link v-if="!r.h" v-for="r in c.regions" :to="'/reports?region='+ r.t" class="">{{r.t}}</router-link>

            </div>
        </div>
        <div class="header tac">
            <h1>Reports 生物报告</h1>
            <!-- <h2>Fully customizable and responsive CSS grids.</h2> -->
            <form class="pure-form searchbar">
                <input type="text" class="pure-input" v-model="q">
                <router-link :to="'/reports?q=' + q " replace>
                    <button type="submit" class="pure-button pure-button-primary">Search</button>
                </router-link>
            </form>
            <div>
                <router-link to="/reports?cid=8&gid=5">ambon</router-link>
                <router-link to="/reports?ds=air%20manis">air manis</router-link>
            </div>
        </div>
        <h2 class="content-subhead">
        海洋生物报告
            <span v-for="(word, key) in cat">{{word}}</span>
            <!-- <span v-if="q">{{q}}</span> -->
        </h2>
        <reports-list :items="items" :grid="true" />
    </div>
</template>
<script>
export default {
    data() {
            var marine;
            var q, rank, name, reports;
            var root = this.$root;
            return {
                q: q,
                cat: {},
                items: [],
                marine: marine,
                name: name,
                page: 1,
                reportsPerPage: 36,
                showSpinner: true,
                loading: false,
                showGrid: true,
                sectionPlace: false,
                keys: null,
                orders: null,
                pswpId: "pswp-",
                Place: root.Place,
                PlaceTree: root.PlaceTree,
            }
        },
        mounted() {
            console.log("reports.vue mounted")
        },
        created() {
            var self = this;
            E.$on('reports-loaded', function(i) {
                console.log("in reports.vue get event reports-loaded", i)
                self.fetchData()

            })
            this.fetchData()
        },
        computed: {
            // items: function(){
            //     var root = this.$root;
            //     return root.getReports(this.$route.query, this.cat);
            // },

        },
        methods: {
            fetchData() {
                var self = this;
                var root = this.$root;
                this.items = root.getReports(this.$route.query, this.cat);
                // this.q = this.cat.q;
                // this.items = result.items;
                // this.cat = result.cat;
                // this.q = result.q;
            }
        },
        watch: {
            // 如果路由有变化，会再次执行该方法
            '$route': 'fetchData',
            '$root.Report': 'fetchData'
        },

}
</script>



